#searchBar {
    background: rgba(92, 79, 168, 0.4);
    border-radius: 1rem;
    width: 45vw;
    position: relative;
}

#search:focus {
    outline: none;
}

#search {
    width: 100%;
    height: 100%;
    border: none;
    background: rgba(0, 0, 0, 0);
    padding: 0 2rem;
    font-size: 1.8rem;
}

.submit {
    position: absolute;
    right: 2rem;
    font-size: 1.3rem;
    top: 53%;
    transform: translateY(-50%);
    background: none !important;
    color: #000 !important;
}